import React, { PropTypes } from 'react';

const Meta = ({ author, createdAt, updatedAt, likesCount }) => (
  <ul className='list-group center-block'>
    <li className='list-group-item'>{`: ${author}`}</li>
    <li className='list-group-item'>{`: ${createdAt}`}</li>
    <li className='list-group-item'>{`: ${updatedAt}`}</li>
    <li className='list-group-item'>{`: ${likesCount}`}</li>
  </ul>
);

Meta.defaultProps = {
  author: ' ',
  createdAt: ' ',
  updatedAt: ' ',
  likesCount: 0
};

Meta.propTypes = {
  author: PropTypes.string,
  createdAt: PropTypes.string,
  updatedAt: PropTypes.string,
  likesCount: PropTypes.number
};

export default Meta;


